<template>
  <div id="professorDetail">
    <div class="detail_wrap clearFix">
      <div class="detail_left">
        <sub-title title="" :link="[{name:'',to: 'carDoctor'},{name:'',to:''}]"></sub-title>
        <div class="introduce_title"></div>
        <div class="introduce_content clearFix">
          <div class="img_wrap">
            <img src="http://static.shanghaiqixiu.org/images/2017/11/7/pic_1512642706078.jpg" alt="">
            <p></p>
          </div>
          <ul>
            <li>
              <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>
              
            </li>
            <li>
              <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>
               
            </li>
            <li>
              <span>:</span>
              
            </li>
            <li>
              <span>:</span>
                FM97.7105.7
            </li>
            <li>
              <span>:</span>
              43441+X   201010101303.4  ZL201320126430.9 
            </li>
          </ul>
        </div>
        <div class="introduce_title"></div>
        <div class="problem_ask clearFix">
          <p></p>
          <textarea name="" id="" v-model="content"></textarea>
          <span style="margin-right: 10px;">:</span>
          <RadioGroup v-model="problemCategory" type="button" size="small">
            <Radio v-for="(item, index) in problems" :key="index" :label="item.dictId">{{item.value}}</Radio>
          </RadioGroup>
          <div class="problem_select" style="float: right;">
            <span style="margin-right: 10px">:</span>
            <Select v-model="problemCategory2" style="width:120px">
              <Option :value="''"></Option>
              <Option :value="''"></Option>
            </Select>
          </div>
          <Button type="primary" size="large" @click="submit" style="position: absolute; right: 10px; bottom: 10px;"></Button>
        </div>
        <div class="introduce_title"></div>
        <div class="problem_jijin">
          <Table border :columns="columns" :data="tableData"></Table>
        </div>
      </div>
      <Professor-list></Professor-list>
    </div>
  </div>
</template>

<script>
export default {
  name: "professorDetail",
  data(){
    return{
      content: '',
      problems: [],
      problemCategory: '',
      problemCategory2: '',
      columns: [
        {
          title: '',
          key: 'problem',
          width: 350
        },
        {
          title: '',
          key: 'date',
          align: 'center',
          width: 100
        },
        {
          title: '',
          key: 'count',
          align: 'center'
        },
        {title: '', key: 'id', align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: () => {
                    console.log(params.row.id)
                    this.$router.push({
                      path:'',
                      query:{id: params.row.id}
                    })
                  }
                }
              }, ''),
            ]);
          }
        }
      ],
      tableData: []
    }
  },
  created(){
    let self=this, systok=localStorage.getItem("SYSTEMTOKEN")
    this.axios({
      method: 'get',
      url: '/cdf/questype/'+ systok,
    }).then(function (res) {
      self.problems= res.data.data
    })
    
    this.getList()
  },
  methods: {
    submit(){
      if(!this.content.trim()){
        this.$Message.error('')
        return
      }
      if(!this.problemCategory){
        this.$Message.error('')
        return
      }
      let self=this, systok=localStorage.getItem("SYSTEMTOKEN"), acctok=localStorage.getItem("ACCESSTOKEN"),
        param={
          accessToken: acctok? acctok: systok,
          anonymity: acctok? false: true,
          content: this.content.trim(),
          questionTypeId: this.problemCategory
        }
      this.axios({
        method: 'post',
        url: '/cdf/addquestion/',
        headers: {'Content-type': 'application/json'},
        data: JSON.stringify(param)
      }).then(function (res) {
        console.log(res.data)
        if(res.data.code=='000000'){
          self.$Message.success("")
          self.getList()
        }
      })
    },
    getList(){
      let self=this, systok=localStorage.getItem("SYSTEMTOKEN")
      this.axios({
        method: 'get',
        url: '/cdf/queryquestionlist/'+ systok,
      }).then(function (res) {
        console.log(res.data)
        let datas= res.data.data
        self.tableData= []
        for(let i in datas){
          self.tableData.push({
            problem: datas[i].content,
            date: (new Date( datas[i].publish).toISOString().split(".")[0].split("T")[0]),
            count: datas[i].views,
            id: datas[i].questionId
          })
        }
      })
    }
  },
}
</script>

<style lang="scss" scoped>
  #professorDetail {
    .clearFix {
      &:after {
        content: '';
        clear: both;
        display: block;
        height: 0;
      }
      zoom: 1;
    }
    .detail_wrap {
      width: 980px;
      margin: 0 auto;
      .detail_left {
        width: 690px;
        min-height: 800px;
        border: 1px solid #d4d4d4;
        margin: 10px 0;
        float: left;
        padding: 10px;
        .introduce_title {
          background-color: rgba(51,137,255,.4);
          margin-top: 10px;
          height: 30px;
          line-height: 30px;
          padding-left: 15px;
          font-weight: 600;
        }
        .introduce_content {
          padding: 15px 15px 0 15px;
          border: 1px solid #ededed;
          .img_wrap {
            width: 127px;
            text-align: center;
            float: left;
            img {
              width: 127px;
              height: 127px;
              margin-bottom: 10px;
            }
            p {
              background: url(../../assets/service/vip.png) no-repeat right center;
              width: 90px;
              margin-left: 10px;
            }
          }
          >ul{
            margin-left: 142px;
            li {
              line-height: 22px;
              margin-bottom: 5px;
              span {
                font-weight: 600;
              }
              
            }
          }
        }
        .problem_ask {
          border: 1px solid #d4d4d4;
          padding: 10px 10px 65px 10px;
          position: relative;
          min-height: 370px;
          p {
            height: 35px;
            line-height: 35px;
            font-size: 18px;
            color: #4ba7f5;
            background: url(../../assets/service/case_ask.jpg) no-repeat left center;
            text-indent: 40px;
          }
          textarea {
            width: 100%;
            max-width: 100%;
            min-width: 100%;
            min-height: 200px;
            border: 1px solid #999;
            margin: 10px 0;
            padding: 10px;
            font-size: 14px;
            outline: none;
          }
          .problem_category_item {
            display: inline-block;
            border: 1px solid #e5e5e5;
            padding: 3px 6px;
            color: #999;
            background-color: #f4f4f4;
            margin-right: 5px;
            &:hover {
              cursor: pointer;
            }
          }
          .active {
            background-color: #1e9fff;
            border: 1px solid #1e9fff;
            color: #fff;
          }
        }
        .problem_jijin {
          min-height: 240px;
          border: 1px solid #d4d4d4;
          padding: 10px;
        }
      }
    }
  }
</style>



